import { getJSON } from "./helper.js";
import * as model from "./model.js";
import ChoroplethView from './view.js'


async function plotMap(container) {
  await model.loadData();

  var data = [
    {
      type: "choroplethmapbox",
      z: [1,2,3],
      locations: ['IND','USA', 'GBR'],
      geojson: model.state.geojson,
    },
  ];

  var layout = {
    mapbox: {
      center: { lon: 17.0, lat: 25.0 },
      zoom: 1.5,
      style: "carto-darkmatter",
      opacity: 1,
    },
    margin: { t: 0, b: 0, l: 0, r: 0 },
    width: screen.availWidth,
    height: screen.availHeight,
    // Remove the coloraxis from the layout:
    coloraxis: false,
  };

  const config = {
    displayModeBar: false,
    responsive: true,
    showlegend: false,
  };

  Plotly.newPlot(container, data, layout, config);
}


function init(){
  ChoroplethView.addMapRendorer(plotMap);
}


init();