import rawCO2 from "../json/annnual_country_co2.json";
import { GEOJSON_URL } from "./config";
import { getJSON } from "./helper";

export const state = {
  geojson: {},
  annualCO2: {},
  year: 2022,
};

export const loadData = async function () {
  state.annualCO2 = rawCO2;
  state.geojson = await getJSON(GEOJSON_URL);
};


